
import { Paella, utils } from 'paella-core';
import getBasicPluginContext from 'paella-basic-plugins';
import getSlidePluginContext from 'paella-slide-plugins';
import getZoomPluginContext from 'paella-zoom-plugin';
import getUserTrackingPluginContext from 'paella-user-tracking';

import packageData from "../package.json";

window.onload = async () => {
    const initParams = {
        customPluginContext: [
            require.context("./plugins", true, /\.js/),
            getBasicPluginContext(),
            getSlidePluginContext(),
            getZoomPluginContext(),
            getUserTrackingPluginContext()
        ],

        // Aqu puedes personalizar las URLs de carga del fichero de configuracin, en el caso de que no lo obtengas
        // de la ubicacin por defecto
        configResourcesUrl: '/paella/',
        configUrl: '/paella/config.json',

        // Aqu puedes personalizar la URL de obtencin de los datos del vdeo, en concreto, la parte esttica de la URL
        repositoryUrl: '/paellarepository/',

        // Esta funcin sirve para que devuelvas el identificador nico del vdeo con el cual puedes obtener sus datos
        // desde tu portal.
        getVideoId: (config, player) => {
            // En la implementacin por defecto, se obtiene del parmetro `id` de la URL, en tu caso puedes personalizar
            // este parmetro para obtener el vdeo desde otro parmetro, desde una cookie o como consideres oportuno
            return location.pathname.split('/').slice(-1)[0];
        },
        // Esta parte es para formar la URL complete de la carga del video. Recibe como parmetro la URL anterior
        // (respositoryUrl), as que en la prctica podras implementar aqu la URL completa y prescindir de
        // la funcin anterior. El parmetro `videoId` se obtiene de la funcin `getVideoId`
        getManifestUrl: (repoUrl,videoId) => {
            if(window.location.href.search("secret") != -1) {
                // return 'secret/paellarepository' + `${videoId}`;
                // return `${repoUrl}${videoId}`;
                return '/secret' + `${repoUrl}${videoId}`;
            } else {
                return `${repoUrl}${videoId}`;
            }
        },

        // Esta funcin devuelve la URL complete del archivo manifest del vdeo. En la implementacin por defecto de
        // paella player, aqu se aade el nombre de archivo /data.json, pero en Opencast no es necesario modificar
        // la url, as que la devolvemos tal cual
        getManifestFileUrl: (manifestUrl) => {
            return manifestUrl;
        },

        // Esta es la funcin donde se realiza la conversin del fichero manifest de opencast al formato de paella player.
        // Seguramente la mayor parte de la implementacin la tendrs que hacer aqu. Ten en cuenta que es una funcin asncrona,
        // as que tendrs que implementarla asncrona o bien utilizar promesas. Por lo que me cuentas, el problema que tienes
        // seguramente estar aqu.

        loadVideoManifest: async function (url, config, player) {
            // Aqu es donde tendras que hacer la llamada a tu portal para obtener los datos
            // del vdeo para formar el manifest en el formato de paella, tal cual indica en la
            // documentacin:
            // https://github.com/polimediaupv/paella-core/blob/main/doc/video_manifest.md
            // Esto es un cdigo de ejemplo, aqu tendras que modificar lo que haga falta para adaptarlo a tu portal
            const response = await fetch(url);
            const pumukitVideoData = await response.json();

            return pumukitVideoData;

        }
    };

    class PaellaPlayer extends Paella {
        get version() {
            const player = packageData.version;
            const coreLibrary = super.version;
            const pluginModules = this.pluginModules.map(m => `${m.moduleName}: ${m.moduleVersion}`);
            return {
                player,
                coreLibrary,
                pluginModules
            };
        }
    }

    const paella = new PaellaPlayer('player-container', initParams);
    paella.loadManifest()
        .then(() => console.log("done"))
        .catch(e => console.error(e));

    // try {
    //     await paella.loadManifest()
    //     console.log("Load done");
    //
    //     await utils.loadStyle('style.css');
    //     console.log("Style loaded using Paella Core API");
    // } catch (e) {
    //     console.error(e);
    // }
}
